@font-face {
  font-family: Xingkai;
  src: url(data:application/x-font-ttf;base64,);
}
html{
  height: 100%;
}
p, div{
  margin: 0;
}
img, a{
  outline: 0;
}
body{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  margin: 0;
  overflow: auto;
  font-size: 10px;
  -webkit-font-smoothing: antialiased;
  -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
  -moz-tap-highlight-color:rgba(0, 0, 0, 0);
  -ms-tap-highlight-color:rgba(0, 0, 0, 0);
  -o-tap-highlight-color:rgba(0, 0, 0, 0);
  tap-highlight-color:rgba(0, 0, 0, 0);
}
p{
  line-height: 100%;
  vertical-align: baseline;
  font-family: "Segoe UI", "Lucida Grande", Helvetica, Arial, "Microsoft YaHei", FreeSans, Arimo, "Droid Sans","wenquanyi micro hei","Hiragino Sans GB", "Hiragino Sans GB W3", Arial, sans-serif;
}
a{
  text-decoration: none;
}
#app{
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.grey,.real{
  position: absolute;
  width: 100%;
  height: 100%;
}
.grey{
  z-index: 0;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
}
.real{
  z-index: 1;
}
.pointer{
  cursor: pointer;
}
.left{
  float: left;
}
.right{
  float: right;
}
.grey-cover, .grey-prompt{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: black;
  opacity: 0.5;
}
.grey-cover{
  z-index: 6;
}
.grey-prompt{
  z-index: 20;
}
.grey-enter-active{
  -webkit-animation: grey .2s linear;
  animation: grey .2s linear;
}
.grey-leave-active{
  -webkit-animation: grey .2s linear reverse;
  animation: grey .2s linear reverse;
}
@-webkit-keyframes grey {
0% {
    opacity: 0;
}
100% {
    opacity: 0.4;
}
}
@keyframes grey {
0% {
    opacity: 0;
}
100% {
    opacity: 0.4;
}
}
.nav-bar{
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
}
.nav-item, .func, .navList-item{
  -webkit-transition: color .2s;
  transition: color .2s;
}
.navList-item:not(:last-of-type){
  border-bottom: 1px solid rgba(255, 255, 255, 0.15);
}
.navList{
  width: calc(100% - 40px);
  background-color: #373D41;
  position: fixed;
  left: 0;
  z-index: 8;
}
.input-bottom{
  position: relative;
  overflow: hidden;
}
.new{
  width: 100%;
  height: 4px;
  background: #1296db;
  margin-left: 1px;
  z-index: 2;
  position: absolute;
}
.input-enter-active{
  -webkit-animation: leave .2s reverse;
  animation: leave .2s reverse;
}
@-webkit-keyframes enter {
0%{
    -webkit-transform: translate3d(100%, 0, 0);
}
100%{
    -webkit-transform: translate3d(0, 0, 0);
}
}
@keyframes enter {
0%{
    -webkit-transform: translate3d(100%, 0, 0);
            transform: translate3d(100%, 0, 0);
}
100%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
}
@-webkit-keyframes leave {
0%{
    -webkit-transform: translate3d(0, 0, 0);
}
100%{
    -webkit-transform: translate3d(-100%, 0, 0);
}
}
@keyframes leave {
0%{
    -webkit-transform: translate3d(0, 0, 0);
            transform: translate3d(0, 0, 0);
}
100%{
    -webkit-transform: translate3d(-100%, 0, 0);
            transform: translate3d(-100%, 0, 0);
}
}
.back-to-top{
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.1);
  position: fixed;
  z-index: 10;
}
.back-to-top>div{
  width: 90%;
  height: 90%;
  position: absolute;
  top: 2%;
  left: 5%;
  background: url(data:image/png;base64,) 50% no-repeat;
  background-size: 60%;
}
.prompt-bar{
  width: 100%;
  text-align: center;
}
.prompt-modal{
  height: auto;
  border-radius: 6px;
  background: white;
  position: fixed;
  z-index: 30;
  display: inline-block;
}
.prompt-modal p{
  font-size: 18px;
  text-align: center;
  line-height: 150%;
}
.prompt-enter-active, .prompt-leave-active{
  -webkit-transition: all .25s;
  transition: all .25s;
}
.prompt-enter, .prompt-leave-to, .contentMode-enter, .contentMode-leave-to{
  opacity: 0;
  -webkit-transform: translate3d(0, -100%, 0);
  transform: translate3d(0, -100%, 0);
}
.contentMode-enter-active, .contentMode-leave-active{
  -webkit-transition: all .3s;
  transition: all .3s;
}
.modal-enter-active{
  -webkit-transition: all .25s;
  transition: all .25s;
}
.modal-enter{
  opacity: 0;
  -webkit-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
}
.products-list{
  width: 100vw;
  position: absolute;
  background: #373D41;
  -webkit-animation: products .3s;
  animation: products .3s;
}
@-webkit-keyframes products {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes products {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@media (max-device-width: 767px) {
.nav-bar{
    position: fixed;
    height: 50px;
}
.list{
    position: fixed;
}
.nav-bar .grey{
    background: #373D41;
    opacity: 0;
}
.name{
    font-size: 20px;
